import React, { useState } from "react";
import {
  Box,
  Button,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalCloseButton,
  useDisclosure,
  Text,
} from "@chakra-ui/react";
import { CheckIcon } from "@chakra-ui/icons";
import FullScreenSection from "./FullScreenSection";
import { useAlertContext } from "../context/alertContext";

const LandingSection = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [isLoading, setIsLoading] = useState(false);
  const [isAvailable, setIsAvailable] = useState(false);
  const [domainName, setDomainName] = useState(""); // State to hold domain name input value
  const { onAlertOpen } = useAlertContext();

  const handleBuyNowClick = () => {
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
      onAlertOpen({
        type: "success",
        message: "Your order placed successfully!",
      });
      setDomainName("");
    }, 2000);
  };

  const checkAvailability = () => {
    if (!isValidDomainName(domainName)) {
      onAlertOpen({
        type: "error",
        message: "Please enter a valid domain name format.",
      });
      return;
    }

    setIsAvailable(true);
    setTimeout(() => {
      setIsAvailable(false);
      onOpen();
    }, 2000);
  };

  const isValidDomainName = (name) => {
    const domainRegex =
      /^(?!:\/\/)([a-zA-Z0-9][a-zA-Z0-9-]{0,61}[a-zA-Z0-9]\.)+[a-zA-Z]{2,63}?$/;
    return domainRegex.test(name);
  };

  return (
    <>
      <Box
        position="relative"
        height="100vh" // Full viewport height to prevent scrolling
        width="100%"
        backgroundImage={require("../images/photo3.jpg")}
        backgroundPosition="center"
        backgroundRepeat="no-repeat"
        backgroundSize="cover"
        backgroundAttachment="fixed"
        display="flex"
        justifyContent="center"
        alignItems="center"
        overflow="hidden" // Ensure no scrollbars
      >
        {/* Transparent Text */}
        <Text
          fontSize="7xl"
          fontWeight="bold"
          color="white"
          backgroundSize="cover"
        >
          Group 3 Dev.ca
        </Text>
      </Box>
    </>
  );
};

export default LandingSection;
